import React, {Component} from 'react';
import logo from '../../assets/image/logo.png';
import './Home.css';
import centerImg from './assets/img.png'

const text = {
    enterRoomName: '방 제목을 입력하세요',
    enterRoomUrl: '방 주소 입력',
    enterRoomPassword: '방 비번을 입력하세요',
    startChatRoom: '시작하기',
    domainUrl: 'www.busk.com/'
}
class Home extends Component {
    constructor(){
        super()
        this.state = {
            roomName: '',
            roomUrl: '',
            roomPassword: ''
        }
        this.updateTextInputState = this.updateTextInputState.bind(this)
        this.goToChatRoom = this.goToChatRoom.bind(this)
    }
    updateTextInputState(stateName, value){
        this.setState({
            [stateName]: value
        })
    }
    goToChatRoom(){
        this.props.history.push('/chat')
    }
    render() {
        const {roomUrl} = this.state
        return (
            <div className={'home-page'}>
                <div className={'logo-wrapper'}>
                    <img src={logo}/>
                </div>
                <div className={'wrapper'}>
                    <img src={centerImg}/>
                    <div className={'input-wrapper'}>
                        <input type='text' placeholder={text.enterRoomName} autoCorrect="off" autoCapitalize="none" autoComplete="off"/>
                        <div className={'url-input'}>
                            <label htmlFor="url">{text.domainUrl}</label>
                            <input id="url" type='text' placeholder={text.enterRoomUrl} onChange={(e)=>console.log(e)} autoCorrect="off" autoCapitalize="none"/>
                        </div>

                        <input type='password' placeholder={text.enterRoomPassword}/>
                    </div>
                    <div className={'bottom-btn-position-wrapper'}>
                        <button className={'bottom-btn'} onClick={this.goToChatRoom}>
                            <span>{text.startChatRoom}</span>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Home;



